<template>
  <div
    class="o-field"
    :class="{ 'is-error': isError }"
  >
    <div class="o-field__tip">{{ title }}</div>
    <input
      class="o-field__input"
      :type="type"
      :password="password"
      :placeholder="placeholder"
      :value="value"
      @input="onInput"
    />
    <div class="o-field__tip has-hairline--t">{{ msg }}</div>
  </div>
</template>

<script>
export default {
  props: {
    type: {
      type: String,
      default: 'text'
    },
    value: {
      type: String,
      default: ''
    },
    password: {
      type: [Boolean, String],
      default: false
    },
    placeholder: {
      type: String,
      default: ''
    },
    title: {
      type: String,
      default: ''
    },
    tip: {
      type: String,
      default: ''
    },
    error: {
      type: String,
      default: ''
    }
  },
  data () {
    return {
      isFocused: false
    }
  },
  computed: {
    title () {
      switch (this.labelMode) {
        case 'none':
          return ''
        case 'auto':
          return this.value ? this.label || this.placeholder : ''
        default:
          return this.label
      }
    },
    isError () {
      return !!this.error
    },
    msg () {
      return this.error || this.tip
    }
  },
  methods: {
    onInput (e) {
      this.$emit('input', e.target.value)
    }
  }
}
</script>

<style lang="scss" scoped>
.o-field {
  &.is-error {
    .o-field__title, .o-field__tip {
      color: $color--error;
    }

    .o-field__tip::after {
      border-color: $color--error;
    }
  }

  &__title {
    height: 40px;
    font-size: 24px;
    color: $color--dark;
    line-height: 40px;
  }

  &__input {
    height: 40px;
    font-size: 32px;
    color: $color--dark;
    line-height: 40px;
    border: none;
    outline: none;
    @include u-font--bold;

    &::-webkit-input-placeholder {
      color: $color;
    }
  }

  &__tip {
    margin-top: 12px;
    height: 40px;
    font-size: 24px;
    color: $color--dark;
    line-height: 40px;
    @include u-font;
  }
}
</style>
