import Vue from 'vue'
import VueRouter from 'vue-router'

import KeepAliveWrapper from '@/components/KeepAliveWrapper.vue'

import TabBar from '@/views/tabbar/TabBar.vue'
import Home from '@/views/tabbar/Home.vue'
import MapView from '@/views/tabbar/Map.vue'
import Park from '@/views/tabbar/Park.vue'
import Account from '@/views/tabbar/Account.vue'


import Filter from '@/views/map/Filter.vue'
import Entity from '@/views/map/Entity.vue'
import Menu from '@/views/map/Menu.vue'

import Weather from '@/views/common/Weather.vue'
import OrderDetail from '@/views/common/OrderDetail.vue'
import Web from '@/views/common/Web.vue'

import Settings from '@/views/settings/Settings.vue'

import SettingsAccount from '@/views/settings/account/Account.vue'
import SettingsAccountEmail from '@/views/settings/account/email/Email.vue'
import SettingsAccountPhone from '@/views/settings/account/phone/Phone.vue'
import SettingsAccountPwd from '@/views/settings/account/change-password/ChangePassword.vue'

import SettingsLanguage from '@/views/settings/language/Language.vue'

import SettingsAbout from '@/views/settings/about/About.vue'
import SettingsAboutParkInfo from '@/views/settings/about/ParkInfo.vue'
import SettingsAboutRule from '@/views/settings/about/Rule.vue'
import SettingsAboutGeneral from '@/views/settings/about/General.vue'

import Sign from '@/views/sign/Sign.vue'
import Login from '@/views/sign/Login.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    component: TabBar,
    children: [
      {
        path: '',
        name: 'home',
        component: Web,
        meta: {
          tabbar: true,
          depth: 1
        }
      },
      {
        path: 'map',
        name: 'map',
        component: MapView,
        meta: {
          tabbar: true,
          depth: 2
        }
      },
      {
        path: 'park',
        name: 'park',
        component: Park,
        meta: {
          tabbar: true,
          depth: 3
        }
      },
      {
        path: 'account',
        name: 'account',
        component: Web,
        meta: {
          tabbar: true,
          depth: 4
        }
      }
    ]
  },
  {
    path: '/map/filter',
    name: 'filter',
    component: Filter
  },
  {
    path: '/entity/:id',
    name: 'entity',
    component: Entity,
    children: [{
      path: 'menu',
      name: 'menu',
      component: Menu
    }]
  },
  {
    path: '/common/weather',
    name: 'weather',
    component: Weather
  },
  {
    path: '/common/order/:id',
    name: 'order-detail',
    component: OrderDetail
  },
  {
    path: '/settings',
    name: 'settings',
    component: Settings,
    meta: {
      group: 'settings',
      depth: 1
    }
  },
  {
    path: '/settings/language',
    name: 'language',
    component: SettingsLanguage
  },
  {
    path: '/settings/account',
    component: KeepAliveWrapper,
    children: [
      {
        path: '',
        name: 'settings-account',
        component: SettingsAccount,
        meta: {
          group: 'settings',
          depth: 3
        }
      },
      {
        path: 'email',
        name: 'settings-account-email',
        component: SettingsAccountEmail,
        meta: {
          group: 'settings',
          depth: 4
        }
      },
      {
        path: 'phone',
        name: 'settings-account-phone',
        component: SettingsAccountPhone,
        meta: {
          group: 'settings',
          depth: 4
        }
      },
      {
        path: 'cpwd',
        name: 'settings-account-cpwd',
        component: SettingsAccountPwd,
        meta: {
          group: 'settings',
          depth: 4
        }
      }
    ]
  },
  {
    path: '/settings/about',
    name: 'about',
    component: SettingsAbout
  },
  {
    path: '/settings/about/park',
    name: 'about-park',
    component: SettingsAboutParkInfo
  },
  {
    path: '/settings/about/rule',
    name: 'about-rule',
    component: SettingsAboutRule
  },
  {
    path: '/settings/about/general',
    name: 'about-general',
    component: SettingsAboutGeneral
  },
  {
    path: '/sign',
    name: 'sign',
    component: Sign
  },
  {
    path: '/login',
    name: 'login',
    component: Login
  },
  {
    path: '*',
    redirect: '/'
  }
]

const router = new VueRouter({
  routes
})

export default router
