<template>
  <list-page
    :title="$t('sign.login.title')"
    :button="$t('sign.login.btn')"
    :disable="disable"
    @click="toLogin"
  >
    <template #footer>
      <div class="is-sub u-align-center">
        {{ $t('sign.noaccount') }}
        <router-link
          class="u-color--blue"
          :to="{ name: 'signup' }"
        >
          {{ $t('sign.tosign') }}
        </router-link>
      </div>
    </template>
    <template #extra>
      <div class="is-sub--blue u-align-center u-separate--farther">
        <router-link
          :to="{ name: 'forget' }"
        >
          {{ $t('sign.login.forget') }}
        </router-link>
      </div>
    </template>
    <field
      class="c-vlist-item--nearer"
      v-model="email"
      :title="$t('sign.login.email')"
      :placeholder="$t('sign.login.email')"
    />
    <field
      class="c-vlist-item--nearer"
      v-model="password"
      :title="$t('sign.login.password')"
      :placeholder="$t('sign.login.password')"
    />
  </list-page>
</template>

<script>
import ListPage from '@/components/list/ListPage.vue'
import Field from '@/components/list/Field.vue'

export default {
  components: {
    ListPage,
    Field
  },
  data () {
    return {
      email: '',
      password: ''
    }
  },
  computed: {
    disable () {
      return !this.email || !this.password
    }
  },
  methods: {
    toLogin () {
      console.log('login')
    }
  }
}
</script>

